import React from "react";
import { Arrow } from "./arrow/Arrow";
import css from "./arrows.module.css";
export const Arrows = () => {
  return (
    <section className={css.box}>
      <div className={css.leftArrow}>
        <Arrow />
      </div>

      <div className={css.rightArrow}>
        <Arrow />
      </div>
    </section>
  );
};
