import React, { useEffect } from "react";
import { useHistory } from "react-router";

import { useLocalStorage, useUtmSource } from "hooks";
import { useWindowSize } from "providers";

import { Basement } from "./components/basement/Basement";
import { Footer } from "./components/footer/Footer";
import { GameWrapper } from "./components/gameWrapper/GameWrapper";
import { Header } from "./components/header/Header";
import { Logo } from "./components/logo/Logo";
import { Menu } from "./components/menu/Menu";
import { Play } from "./components/play/Play";
import { Roof } from "./components/roof/Roof";
import { Steps } from "./components/steps/Steps";
import css from "./promo.module.css";

const referralReg = /\?user_id=([0-9]{1,})/;
const sourceReg = /source=(\w{1,})/;

const CrashPromo = () => {
  const { width } = useWindowSize();
  const [token] = useLocalStorage("token");
  const history = useHistory();

  const [refer, setRefer] = useLocalStorage("refer");
  const [sourceRefer, setSourceRefer] = useLocalStorage("sourceRefer");
  useEffect(() => {
    if (!token) {
      const search = history.location.search;

      const referralResult = search.match(referralReg);
      const sourceResult = search.match(sourceReg);

      if (referralResult) {
        const [url, referralId] = referralResult;

        setRefer(referralId);
      }

      if (sourceResult) {
        const [url, sourceReferralId] = sourceResult;
        setSourceRefer(sourceReferralId);
      }
    }
  }, []);
  useUtmSource();

  return (
    <section className={css.background}>
      <div className={css.gradient} />
      <div className={css.roof}>
        <Roof width={width} />
        <Logo />
        <Header />
      </div>

      <GameWrapper />

      <div className={css.basement}>
        <Menu />
        <Basement width={width} />

        <footer>
          <Footer />
          <Steps />
        </footer>
      </div>

      <div className={css.terms}>
        <Play />
      </div>
    </section>
  );
};

export default CrashPromo;
