import React from "react";
import css from "./basement.module.css";

export const Basement = ({ width }) => {
  return (
    <section className={css.box}>
      {width > 450 ? (
        <img
          src="/crash-data/img/roof/LineUpBG.svg"
          alt="lines"
          className={css.img}
        />
      ) : (
        <img
          src="/crash-data/img/roof/LineMini.svg"
          alt="lines"
          className={css.img}
        />
      )}
    </section>
  );
};
