import React from "react";
import { Content } from "./content/Content";
import css from "./footer.module.css";
import { Icon } from "./icon/Icon";

export const Footer = () => {
  return (
    <section className={css.box}>
      <Icon direction={"left"} />
      <Content />
      <Icon direction={"right"} />
    </section>
  );
};
