import React from "react";
import { Step } from "./step/Step";
import css from "./steps.module.css";

const steps = [
  "Place a Bet",
  "Wait for the Game to Start",
  "Cashout Your Bet Before the Graph Crashes",
];

export const Steps = () => {
  return (
    <section className={css.box}>
      {steps.map((value, i) => (
        <Step key={`${value}_${i + 1}`} number={i + 1}>
          {value}
        </Step>
      ))}
    </section>
  );
};
