import React from "react";
import css from "./content.module.css";

export const Content = () => {
  return (
    <div className={css.box}>
      <div className={css.upText}>
        <div className={css.adviceWrapper}>
          <img
            loading="lazy"
            src="/crash-data/img/footer/advice.svg"
            alt="Mega win!"
            className={css.adviceImg}
          />
        </div>
      </div>
    </div>
  );
};
