import React from "react";
import css from "./icon.module.css";

export const Icon = ({ direction }) => {
  return (
    <div className={css.box}>
      <img
        loading="lazy"
        className={`
          ${css.img}
          ${direction === "left" ? css.img_left : css.img_right}
        `}
        src="/crash-data/img/footer/lighting.svg"
        alt="lighting"
      />
    </div>
  );
};
